<template>

    <tituloBusqueda titulo="Consulta de Maestros"
        descripcion="Realiza la busqueda de un maestro de acuerdo a los filtros."></tituloBusqueda>

    <div class="formularios">
        <div class="row">
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="curp" @keypress="valida_curp" @focus="limpia" class="form-control"
                        aria-describedby="id" placeholder="CURP">
                </div>
            </div>
            <div class="col">
                <input_seccion @seccion_val="(n)=>seccion=n"></input_seccion>
            </div>
            <div class="col">
                <input_nomina @nomina_val="(n)=>nomina=n"></input_nomina>
            </div>
        </div>
        <div class="row">
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="paterno" @keypress="valida_paterno" @keyup="mayuscula()" @focus="limpia"
                        class="form-control" aria-describedby="id" placeholder="APELLIDO PATERNO">
                </div>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="materno" @keypress="valida_materno" @keyup="mayuscula()" @focus="limpia"
                        class="form-control" aria-describedby="curp" placeholder="APELLIDO MATERNO">
                </div>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="nombre" @keypress="valida_nombre" @keyup="mayuscula()" @focus="limpia"
                        class="form-control" aria-describedby="rfc" placeholder="NOMBRE">
                </div>
            </div>
        </div>
        <div class="buttons ">
            <a class="btn-outline primary" @click="buscarmaestros">
                Buscar
            </a>
        </div>

    </div>

    <table class="table table-hover tbl-resultados">
        <thead>
            <tr>
                <th scope="col">#</th>
                <th scope="col">CURP</th>
                <th scope="col">PATERNO</th>
                <th scope="col">MATERNO</th>
                <th scope="col">NOMBRE</th>
                <th scope="col">SECC</th>
                <th scope="col"></th>
                <th scope="col"></th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(mtro,index) in arr_maestros" :key="{index}">
                <th scope="row" style="vertical-align: middle">{{index+1}}</th>
                <td>
                    <input type="text" disabled="true" :id="mtro.id_maestro+'_curp'" :value="mtro.curp"
                        @keypress="valida_curp" class="form-control" aria-describedby="id" placeholder="CURP">
                </td>
                <td>
                    <input type="text" disabled="true" :id="mtro.id_maestro+'_pat'" :value="mtro.paterno"
                        @keypress="valida_paterno" @keyup="mayuscula()" @focus="limpia" class="form-control"
                        aria-describedby="id" placeholder="APELLIDO PATERNO">
                </td>
                <td>
                    <input type="text" disabled="true" :id="mtro.id_maestro+'_pat'" :value="mtro.materno"
                        @keypress="valida_paterno" @keyup="mayuscula()" @focus="limpia" class="form-control"
                        aria-describedby="id" placeholder="APELLIDO MATERNO">
                </td>
                <td>
                    <input type="text" disabled="true" :id="mtro.id_maestro+'_pat'" :value="mtro.nombre"
                        @keypress="valida_paterno" @keyup="mayuscula()" @focus="limpia" class="form-control"
                        aria-describedby="id" placeholder="NOMBRE">
                </td>
                <td>{{mtro.seccion}}</td>
                <td>
                    <!--<a class="btn-outline primary" style="cursor:pointer" :id="mtro.id_maestro+'btn'" @click="guardar">Editar</a>-->
                    <img class="img" title="Editar" :id="mtro.id_maestro+'btn'" @click="guardar"
                        src="@/assets/iconos/editar2.png">
                </td>
                <td><img class="img" title="Guardar" src="@/assets/iconos/check5.png"></td>
            </tr>

        </tbody>
    </table>
    <miModal titulo='PLAZAS' :mensaje=mensajeModal></miModal>
    <myFooter />

</template>

<script>

import { defineAsyncComponent } from 'vue'
import axios from 'axios'

const input_seccion = defineAsyncComponent(() => import('@/modules/shared/inputs/seccion.vue'))
const input_nomina = defineAsyncComponent(() => import('@/modules/shared/inputs/nomina.vue'))
const myFooter = defineAsyncComponent(() => import('@/modules/shared/components/Footer.vue'))
const tituloBusqueda = defineAsyncComponent(() => import('@/modules/shared/components/TituloBusqueda.vue'))
const miModal = defineAsyncComponent(() => import('@/modules/shared/components/ModalAceptar.vue'))

let curp = ''
let paterno = ''
let materno = ''
let nombre = ''
let arr_maestros = []
let seccion = ''
let nomina = ''

let mensajeModal = ''

export default ({

    components: {
        input_seccion,
        input_nomina,
        myFooter,
        tituloBusqueda,
        miModal
    },

    data() {
        return {
            curp, paterno, materno, nombre, seccion, nomina,
            mensajeModal,
            arr_maestros: []
        }
    },
    methods: {
        async buscarmaestros() {

            let params = {

                seccion: this.seccion,
                curp: this.curp,
                ap_pat: this.paterno,
                ap_mat: this.materno,
                nombre: this.nombre,
                nomina: this.nomina
            }
            console.log("Buscando..")
            try {
                const response = await this.obtenerMaestros(params)
                const { estatus, errorText, num_registros, resultado } = response

                console.log(response)

                if (estatus === 'error') {
                    this.mensajeModal = errorText
                    $('#staticBackdrop2').modal('show')
                }

                if (estatus === 'ok') {
                    if (num_registros < 1) {
                        this.mensajeModal = 'No se encontro informaciÃ³n'
                        $('#staticBackdrop2').modal('show')
                    }
                    else {
                        this.arr_maestros = response.resultado
                        console.log("RESULTADO::", resultado)
                    }
                }

            } catch (error) {
                console.log(error)
                this.mensajeModal = error.data.errorText
                $('#staticBackdrop2').modal('show')
            }


        },
        async obtenerMaestros(params) {
            try {
                var json = JSON.stringify(params);

                console.log(json)
                const response = await axios.get(`http://localhost:8081/api/maestro`, {
                    params: {
                        seccion: this.seccion,
                        curp: this.curp,
                        ap_pat: this.paterno,
                        ap_mat: this.materno,
                        nombre: this.nombre,
                        nomina: this.nomina
                    }
                })
                return response.data
            }
            catch (error) {
                console.log(error)
                return error.response.data
            }

        },
        valida_curp(e) {
            if (this.curp.length >= 18)
                e.preventDefault()
        },
        valida_paterno(e) {
            if (this.paterno.length >= 25)
                e.preventDefault()
        },
        valida_materno(e) {
            if (this.materno.length >= 25)
                e.preventDefault()
        },
        valida_nombre(e) {
            if (this.nombre.length >= 25)
                e.preventDefault()
        },
        mayuscula() {
            this.nombre = this.nombre.toUpperCase()
            this.paterno = this.paterno.toUpperCase()
            this.materno = this.materno.toUpperCase()
        },
        limpia(e) {
            //e.srcElement.value = ''
        },
        guardar(e) {
            let habilitar = $('#' + e.srcElement.id.substring(0, 7) + '_curp').prop("disabled")

            //$('#'+ e.srcElement.id.substring(0,7) ).prop("disabled",!habilitar)

            $('[id^="' + e.srcElement.id.substring(0, 7) + '"]').prop("disabled", !habilitar)
        },
        editar(e) {
            $('#staticBackdrop2').modal('show')
            //$( e. ).prop( "disabled", true );
        }

    }

})

</script>


<style scoped>
    table {
        margin: 35px;
        width: 835px;
    }
    
    thead {
        background-color: var(--color-primary-2);
        color: var(--color-white);
       
        padding: 5px;
        border-radius: 5px;
    }
    
    tbody {
        min-height: 200px;
        max-height: 450px;
        overflow-y: scroll;
    
        padding-left: 5px;
        overflow-x: hidden;
        font-size: smaller;
    }
    
    tbody tr {
        border-bottom: var(--color-grey-2) solid 1px;
        padding: 5px;
        background-color: var(--color-white);
        border-radius: 5px;
    }
    
    
    .tbl-resultados {
        font-size: var(--font-size);
        margin-top: 20px;
        text-align: center;
        /*overflow: scroll;*/
    }
    .tbl-resultados .tbl-header {
        background-color: var(--color-primary-2);
        color: var(--color-white);
        display: flex;
        height: 40px;
        padding: 5px;
        border-radius: 5px;
        /*border: var(--color-primary-3) solid 1px;*/
    }
    
    
    .tbl-header-data {
        margin: 0px 5px;
        padding: 2px;
        width: 120px;
    }
    
    .tbl-resultados .tbl-data {
        border-bottom: var(--color-grey-2) solid 1px;
        padding: 5px;
        background-color: var(--color-white);
        border-radius: 5px;
    }
    
    .tbl-data:nth-of-type(odd) {
        opacity: 70%;
    }
    
    .tbl-data:nth-of-type(even) {
        opacity: 100%;
    }
    
    .tbl-maestros {
        min-height: 200px;
        max-height: 450px;
        overflow-y: scroll;
    
        padding-left: 5px;
        overflow-x: hidden;
        font-size: smaller;
    }
    
    .tbl-dato {
        width: 120px;
        margin: 0px 5px;
        padding: 2px;
        text-align: center;
    }
    
    .tbl_short {
        width: 40px;
    }
    
    .tbl_short-s {
        width: 10px;
    }
    
    .tbl_long {
        width: 150px;
    }

    td{
        vertical-align: middle;
        padding-inline-start: initial;
    }

    td input{
        font-size: small;
    }

    .input_disables{
        border: 1px;;
    }

    td input:disabled {
        border:none;
        background: none;
    }

    .img{
        transition: 0.3s;
        padding: 2px;
        border-radius: 6px;
        cursor:pointer
    }

    .img:hover {
        opacity:10;
        background-color:whitesmoke ;
    }

    
    </style>